<template>
    <div class="salary">
        <div v-for="(salary, index) in sortedSalaries">
            <div> : {{salary.month}}</div>
            <div>   {{salary.period}}</div>
            <div>: {{salary.user.username}}</div>
            <div> : {{salary.salary}} </div>
            <table class="salary-table">
                <thead>
                <tr class="salary-head">
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                </thead>
                <tbody class="salary-body">
                <tr>
                    <td><strong></strong></td>
                </tr>
                <tr>
                    <td>  </td>
                    <td>{{salary.month}}</td>
                    <td>{{salary.days}}</td>
                    <td>{{salary.hours}}</td>
                    <td>{{salary.salary}}</td>
                </tr>
                <tr>
                    <td> </td>
                    <td></td>
                    <td>{{salary.days}}</td>
                    <td>{{salary.hours}}</td>
                    <td>{{salary.salary}}</td>
                </tr>
                <tr>
                    <td><strong></strong></td>
                </tr>
                <tr>
                    <td> </td>
                    <td>{{salary.month}}</td>
                    <td></td>
                    <td></td>
                    <td>{{salary.ipn}}</td>
                </tr>
                <tr>
                    <td> </td>
                    <td>{{salary.month}}</td>
                    <td></td>
                    <td></td>
                    <td>{{salary.opv}}</td>
                </tr>
                <tr>
                    <td>  </td>
                    <td>{{salary.month}}</td>
                    <td></td>
                    <td></td>
                    <td>{{salary.vosms}}</td>
                </tr>
                <tr>
                    <td><strong> </strong></td>
                </tr>
                <tr>
                    <td> </td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td>{{salary.withheld}}</td>
                </tr>
                <tr>
                    <td> </td>
                    <td>{{salary.month}}</td>
                    <td></td>
                    <td></td>
                    <td>{{salary.opv}}</td>
                </tr>
                <tr>
                    <td> </td>
                    <td>{{salary.month}}</td>
                    <td></td>
                    <td></td>
                    <td>{{salary.withheld}}</td>
                </tr>
                <tr>
                    <td> </td>
                    <td>{{salary.month}}</td>
                    <td></td>
                    <td></td>
                    <td>{{salary.vosms}}</td>
                </tr>
                <tr>
                    <td> </td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td>{{salary.total_nalog}}</td>
                </tr>
                <tr>
                    <td>     </td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td>{{salary.debt}}</td>
                </tr>
                </tbody>
            </table>
            <hr style="border: 1px solid black">
        </div>
    </div>
</template>

<script>
    export default {
        name: "Salary",
        created() {
            document.title = "Salary";
        },
        async mounted() {
            await this.$store.dispatch("fetchSalaries", this.$store.state.auth.user.id);
        },
        computed: {
            sortedSalaries: function () {
                function compare(a, b) {
                    if (a.id > b.id)
                        return -1;
                    if (a.id < b.id)
                        return 1;
                    return 0;
                }

                return this.$store.getters.getSalaries.sort(compare);
            }
        }
    }
</script>

<style scoped>
    @import '../../assets/css/salary.css';
</style>
